import { Component } from '@angular/core';
import { faCoffee } from '@fortawesome/free-solid-svg-icons';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  status: boolean[] = [true, false, false,false,false,false];
  classe: string[] = ["active", "", "","","",""];
  cheminImage:any = '/enp.png';
  title = 'mon-projet';
  faCoffee = faCoffee;
  
 getStatus(i) :boolean
  {
    return this.status[i] ;
  }
  getClass(i) :string
  {
    return this.classe[i] ;
  }
  active(i)
  {
    let j;
    for(j=0;j<this.status.length;j++)
    
    {
      this.status[j]=false;
      this.classe[j]="";
    }
    this.status[i]=true;
    this.classe[i]="active";
  }
  setStatus(i)
  {
    let j;
    this.status[i]=true;
    for(j=0;j<this.classe.length;j++)
    
    {
      this.classe[j]="";
    }
    this.classe[i]="active";
  }
}
